import { Component, OnInit } from "@angular/core";
import { ServiceService } from "../services/services.service";
import { AngularFireStorage } from "@angular/fire/storage";
import { AuthService } from "../services/auth.service";
import { AngularFirestore } from "@angular/fire/firestore";
import { AngularFireAuth } from "@angular/fire/auth";

@Component({
  selector: "app-account",
  templateUrl: "./account.component.html",
  styleUrls: ["./account.component.css"],
})
export class AccountComponent implements OnInit {
  services: any;
  myservices: any;
  service: any;
  state: any;
  query: string;
  requery: string;
  has = 0;
  exist = false;
  public search: any = "";
  p: number = 1;
  constructor(
    public api: ServiceService,
    private storage: AngularFireStorage,
    public auth: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth
  ) {}

  ngOnInit() {
    this.getservices();
    this.getmyservices();
  }

  getservices() {
    this.api.read_Service().subscribe((data) => {
      this.services = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          name: e.payload.doc.data()["name"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: no-string-literal
          parametrage: e.payload.doc.data()["parametrage"],

          // tslint:disable-next-line: no-string-literal
          value: e.payload.doc.data()["value"],

          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log(this.services);
    });
  }
  getmyservices() {
    this.api.read_myService().subscribe((data) => {
      this.myservices = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          name: e.payload.doc.data()["name"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: no-string-literal
          parametrage: e.payload.doc.data()["parametrage"],

          // tslint:disable-next-line: no-string-literal
          value: e.payload.doc.data()["value"],

          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log("mine: ", this.myservices);
    });
  }
  add(serv) {
    for (let s of this.myservices) {
      if (serv.name == s.name) {
        this.exist = true;
      }
    }
    if (this.exist == false) {
      let se = Object.assign({}, serv);
      this.api.create_NewService(se);
    } else {
      alert("le service " + serv.name + " existe déjà");
      //window.location.reload();
      this.exist = false;
      this.requery = serv.name;
    }
  }
  up(service) {
    let message = "";

    if (service.value == "on") {
      service.value = "off";
      message =
        "vous êtes sûre de vouloir désactiver le service " + service.name;
    } else {
      service.value = "on";
      message = "vous êtes sûre de vouloir activer le service " + service.name;
    }
    if (confirm(message)) {
      let se = Object.assign({}, service);
      this.api.update_Service(service.id, se);
    }
  }
}
