import { Component, OnInit } from "@angular/core";
import { AngularFireStorage } from "@angular/fire/storage";
import { map, finalize } from "rxjs/operators";
import { Observable } from "rxjs";
import { Produitservice } from "../services/produit.service";
import { Produit } from "../classes/produit";
import { AuthService } from "../services/auth.service";
import { AngularFirestore } from "@angular/fire/firestore";
import { AngularFireAuth } from "@angular/fire/auth";
@Component({
  selector: "app-products",
  templateUrl: "./products.component.html",
  styleUrls: ["./products.component.css"],
})
export class ProductsComponent implements OnInit {
  produits: any;
  produit: Produit;
  s = 0;
  state: any;
  public search: any = "";
  p: number = 1;
  downloadURL: Observable<string>;
  selectedFile: File = null;
  fb = "";
  userData: any; // Save logged in user data
  cuse: any;
  selected: Produit;
  ss = false;
  constructor(
    public produitService: Produitservice,
    private storage: AngularFireStorage,
    public auth: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth
  ) {}

  ngOnInit() {
    this.produit = new Produit();
    this.getproduits();
    this.userdet();
    this.selected = new Produit();
    this.selected.photo =
      "https://firebasestorage.googleapis.com/v0/b/paygate-2883d.appspot.com/o/90x90.jpg?alt=media&token=99d0cf7f-3b95-46c6-a501-673e69a92a9b";
  }

  userdet() {
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;

        //  console.log("my datas:", JSON.stringify(this.userData));
        this.cuse = this.userData;
        this.produit.vendeur = user.displayName;
        // localStorage.setItem("user", JSON.stringify(this.userData));
        //JSON.parse(localStorage.getItem("user"));
      } else {
        //localStorage.setItem("user", null);
        //JSON.parse(localStorage.getItem("user"));
        window.location.replace("login");
      }
    });
  }
  test(selected) {
    this.produit.photo = selected.photo;
    if (this.state == true) {
      this.produit.etat = "en cours";
    } else this.produit.etat = "désactiver";
  }

  onFileSelected(event) {
    var n = Date.now();
    const file = event.target.files[0];
    const filePath = `/Products/${n}`;
    const fileRef = this.storage.ref(filePath);
    const task = this.storage.upload(`/Products/${n}`, file);
    task
      .snapshotChanges()
      .pipe(
        finalize(() => {
          this.downloadURL = fileRef.getDownloadURL();
          this.downloadURL.subscribe((url) => {
            if (url) {
              this.fb = url;
            }
            console.log(this.fb);
          });
        })
      )
      .subscribe((url) => {
        if (url) {
          console.log(url);
        }
      });
  }
  add() {
    this.produit.photo = this.fb;
    this.produit.id_vd = localStorage.getItem("user");
    this.produit.etat = "en cours";
    this.produit.prixact = this.produit.prix;
    this.produit.dt = Date();
    let pr = Object.assign({}, this.produit);
    this.produitService.create_NewProduit(pr);
    alert("ajouté avec succés");
    this.produit = new Produit();
    // window.location.reload();
  }
  up() {
    this.produit.photo = this.fb;
    this.produit.id_vd = localStorage.getItem("user");
    this.produit.dt = Date();
    let pr = Object.assign({}, this.produit);
    this.produitService.update_Produit(this.produit.id, pr);
    //  alert("ajouté avec succés");
    this.produit = new Produit();
    // window.location.reload();
  }
  getproduits() {
    this.produitService.read_mesProduits().subscribe((data) => {
      this.produits = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          titre: e.payload.doc.data()["titre"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: prix-string-literal
          prix: e.payload.doc.data()["prix"],
          prixact: e.payload.doc.data()["prixact"],
          acheteur: e.payload.doc.data()["acheteur"],
          // tslint:disable-next-line: no-string-literal
          description: e.payload.doc.data()["description"],
          etat: e.payload.doc.data()["etat"],

          // tslint:disable-next-line: no-string-literal
          photo: e.payload.doc.data()["photo"],
          dt: e.payload.doc.data()["dt"],
          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log(this.produits);
    });
  }

  delete(id): void {
    if (confirm("êtes vous sûre de vouloir supprimer?")) {
      this.produitService.delete_Produit(id);
      //window.location.replace('gestion');
      this.selected = new Produit();
    }
  }
  annuler() {
    this.produit = new Produit();
    this.selected = new Produit();
    this.ss = false;
  }
  select(us) {
    this.ss = true;
    this.selected = us;
    this.produit = us;
    if (us.etat == "en cours") {
      this.state = true;
    } else this.state = false;
  }
}
