import { Component, OnInit } from "@angular/core";
import { AuthService } from "../services/auth.service";
import { AngularFirestore } from "@angular/fire/firestore";
import { AngularFireAuth } from "@angular/fire/auth";
import { UserService } from "../services/user.service";
import { AngularFireStorage } from "@angular/fire/storage";
import { ServiceService } from "../services/services.service";
import { User } from "../classes/user";
import { Produitservice } from "../services/produit.service";

@Component({
  selector: "app-dashboard",
  templateUrl: "./dashboard.component.html",
  styleUrls: ["./dashboard.component.css"],
})
export class DashboardComponent implements OnInit {
  cuser: any;
  users: any;
  user: User;
  uid: string;
  public search: any = "";
  p: number = 1;
  profile: any;
  gain = 0;
  query: string;
  days = 0;
  photo = "";
  edit = false;
  myservices: any;
  userData: any; // Save logged in user data
  cuse: any;
  nb = 0;
  balance = 0;

  exist = false;
  produits: any;
  transactions = 0;
  incomes = 0;
  services: any;
  constructor(
    public auth: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth,
    public userService: UserService,
    private storage: AngularFireStorage,
    public api: ServiceService,
    public produitService: Produitservice
  ) {}

  ngOnInit() {
    this.userdet();
    this.cuse = null;
    this.cuser = localStorage.getItem("user");
    console.log(this.cuser);
    //console.log("user: ", this.cuser.photoURL);
    this.user = new User();
    this.users = [];
    this.getservices();
    this.getUsers();

    this.getproduits();
    this.getmyservices();
    // console.log(this.myservices.length);
  }
  getUsers() {
    this.userService.read_User().subscribe((data) => {
      this.users = data.map((e) => {
        return {
          uid: e.payload.doc.id,

          email: e.payload.doc.data()["email"],
          photoURL: e.payload.doc.data()["photoURL"],
          displayName: e.payload.doc.data()["displayName"],
          emailVerified: e.payload.doc.data()["emailVerified"],
          phoneNumber: e.payload.doc.data()["phoneNumber"],
          adresse: e.payload.doc.data()["adresse"],
          bio: e.payload.doc.data()["bio"],
        };
      });
      // console.log(this.users);
      for (let u of this.users) {
        if (this.cuser === u.uid) {
          /* localStorage.setItem("profile", JSON.stringify(u));
          this.profile = JSON.parse(localStorage.getItem("profile"));*/
          this.profile = u;
          // console.log(this.profile);
        }
      }
    });
  }
  getproduits() {
    this.produitService.read_mesProduits().subscribe((data) => {
      this.produits = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          titre: e.payload.doc.data()["titre"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: no-string-literal
          url: e.payload.doc.data()["url"],

          // tslint:disable-next-line: no-string-literal
          description: e.payload.doc.data()["description"],
          etat: e.payload.doc.data()["etat"],

          // tslint:disable-next-line: no-string-literal
          photo: e.payload.doc.data()["photo"],
          prixact: e.payload.doc.data()["prixact"],
          prix: e.payload.doc.data()["prix"],
          dt: e.payload.doc.data()["dt"],
          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log(this.produits);
      for (let p of this.produits) {
        this.gain = this.gain + parseInt(p.prixact);
        this.balance = this.balance + (parseInt(p.prixact) - parseInt(p.prix));
      }
    });
  }
  userdet() {
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        //  console.log("my datas:", JSON.stringify(this.userData));
        this.cuse = this.userData;
        let d = new Date();
        console.log("date: ", d);
        let jour = new Date(this.cuse.metadata.creationTime);
        console.log("sub: ", jour);

        console.log(jour.toUTCString());
        this.days += d.getDate() - jour.getDate();
        this.days += (d.getMonth() - jour.getMonth()) * 30;
        this.days += (d.getFullYear() - jour.getFullYear()) * 356;

        console.log("difference: ", this.days);
        // localStorage.setItem("user", JSON.stringify(this.userData));
        //JSON.parse(localStorage.getItem("user"));
      } else {
        //localStorage.setItem("user", null);
        //JSON.parse(localStorage.getItem("user"));
        window.location.replace("login");
      }
    });
  }

  getservices() {
    this.api.read_Service().subscribe((data) => {
      this.services = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          name: e.payload.doc.data()["name"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: no-string-literal
          parametrage: e.payload.doc.data()["parametrage"],

          // tslint:disable-next-line: no-string-literal
          value: e.payload.doc.data()["value"],

          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log(this.services);
    });
  }
  gotoaccount() {
    window.location.replace("account");
  }
  gotoweb() {
    window.location.replace("websites");
  }
  add(serv) {
    for (let s of this.myservices) {
      if (serv.name == s.name) {
        this.exist = true;
      }
    }
    if (this.exist == false) {
      let se = Object.assign({}, serv);
      this.api.create_NewService(se);
    } else {
      alert("le service " + serv.name + " existe déjà");
      //window.location.reload();
      this.exist = false;
      this.query = serv.name;
    }
  }
  getmyservices() {
    this.api.read_myService().subscribe((data) => {
      this.myservices = data.map((e) => {
        return {
          id: e.payload.doc.id,

          // tslint:disable-next-line: no-string-literal
          name: e.payload.doc.data()["name"],
          // tslint:disable-next-line: no-string-literal

          // tslint:disable-next-line: no-string-literal
          parametrage: e.payload.doc.data()["parametrage"],

          // tslint:disable-next-line: no-string-literal
          value: e.payload.doc.data()["value"],

          // tslint:disable-next-line: no-string-literal
        };
      });
      console.log("mine: ", this.myservices);
      this.nb = this.myservices.length;
      localStorage.setItem("services", this.nb.toString());
      //alert(this.nb);
    });
  }
}
