import { Component, OnInit } from "@angular/core";
import { AuthService } from "../services/auth.service";
import { AngularFirestore } from "@angular/fire/firestore";
import { AngularFireAuth } from "@angular/fire/auth";
import { UserService } from "../services/user.service";
import { AngularFireStorage } from "@angular/fire/storage";

@Component({
  selector: "app-tokens",
  templateUrl: "./tokens.component.html",
  styleUrls: ["./tokens.component.css"],
})
export class TokensComponent implements OnInit {
  cuse: any;
  userData: any;
  users: any;
  profile: any;
  cuser: any;
  random = "0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ";

  constructor(
    public auth: AuthService,
    public afs: AngularFirestore, // Inject Firestore service
    public afAuth: AngularFireAuth,
    public userService: UserService,
    private storage: AngularFireStorage
  ) {}
  ngOnInit() {
    this.userdet();
    this.getUsers();
  }
  userdet() {
    this.afAuth.authState.subscribe((user) => {
      if (user) {
        this.userData = user;
        //  console.log("my datas:", JSON.stringify(this.userData));
        this.cuse = this.userData;
      } else {
        //localStorage.setItem("user", null);
        //JSON.parse(localStorage.getItem("user"));
        window.location.replace("login");
      }
    });
  }
  randomString(length, chars) {
    var result = "";
    for (var i = length; i > 0; --i)
      result += chars[Math.floor(Math.random() * chars.length)];
    return result;
  }
  getUsers() {
    this.userService.read_User().subscribe((data) => {
      this.users = data.map((e) => {
        return {
          uid: e.payload.doc.id,

          email: e.payload.doc.data()["email"],
          photoURL: e.payload.doc.data()["photoURL"],
          displayName: e.payload.doc.data()["displayName"],
          emailVerified: e.payload.doc.data()["emailVerified"],
          phoneNumber: e.payload.doc.data()["phoneNumber"],
          adresse: e.payload.doc.data()["adresse"],
          bio: e.payload.doc.data()["bio"],
          token: e.payload.doc.data()["token"],
          apisecret: e.payload.doc.data()["apisecret"],
        };
      });
      // console.log(this.users);
      for (let u of this.users) {
        if (localStorage.getItem("user") === u.uid) {
          /* localStorage.setItem("profile", JSON.stringify(u));
          this.profile = JSON.parse(localStorage.getItem("profile"));*/
          this.profile = u;
          console.log(this.profile);
          if (this.profile.token === null) {
            console.log("no");
            this.renew();
          }
        }
      }
    });
  }
  renew() {
    const apikey = this.randomString(24, this.random);
    const apisecret = this.randomString(32, this.random);
    this.profile.token = apikey;
    this.profile.apisecret = apisecret;
    console.log("tkey", apikey);
    console.log("tsec", apisecret);
    this.userService.update_User(this.profile.uid, this.profile);
  }
}
